.AppSearch {
  display: flex; }
  .AppSearch form {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    width: 100%; }
  .AppSearch form * {
    border: 2px solid white;
    font-size: 18px;
    font-weight: 900;
    margin: 0;
    color: white; }
    .AppSearch form *:focus {
      outline: none;
      border: 1px solid white;
      box-shadow: 0 0 0 1px white, 0 0 0 3px wheat, 0 0 0 4px white; }
  .AppSearch .search-input {
    width: 70%;
    height: 100%;
    max-height: 100%;
    background-color: transparent;
    padding: 0 10px;
    box-sizing: border-box;
    width: 100%;
    transition: all 100ms ease-in-out;
    transition-property: border, box-shadow; }
  .AppSearch .search-btn {
    width: 150px;
    height: 100%;
    background-color: transparent;
    border-left: none;
    cursor: pointer; }

/**
 * ----------------------------------------
 * slide-in-bottom
 * ----------------------------------------
 */
@-webkit-keyframes slide-in-bottom {
  0% {
    transform: translateY(70px);
    opacity: 0; }
  100% {
    transform: translateY(0);
    opacity: 1; } }
@keyframes slide-in-bottom {
  0% {
    transform: translateY(70px);
    opacity: 0; }
  100% {
    transform: translateY(0);
    opacity: 1; } }

/**
 * ----------------------------------------
 * slide-down-top
 * ----------------------------------------
 */
@-webkit-keyframes slide-in-top {
  0% {
    transform: translateY(-25px);
    opacity: 0; }
  100% {
    transform: translateY(0);
    opacity: 1; } }
@keyframes slide-in-top {
  0% {
    transform: translateY(-25px);
    opacity: 0; }
  100% {
    transform: translateY(0);
    opacity: 1; } }

/**
 * ----------------------------------------
 * slide-down-top
 * ----------------------------------------
 */
@-webkit-keyframes fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
@keyframes fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

/**
 * ----------------------------------------
 * kenburns-top
 * ----------------------------------------
 */
@-webkit-keyframes kenburns-top {
  0% {
    transform: scale(1) translateY(0);
    transform-origin: 50% 16%; }
  100% {
    transform: scale(1.2) translateY(-10px);
    transform-origin: top; } }
@keyframes kenburns-top {
  0% {
    transform: scale(1) translateY(0);
    transform-origin: 50% 16%; }
  100% {
    transform: scale(1.2) translateY(-10px);
    transform-origin: top; } }

/**
 * ----------------------------------------
 * bouncing
 * ----------------------------------------
 */
@-webkit-keyframes bouncing {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(20px); }
  100% {
    transform: translateY(0); } }
@keyframes bouncing {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(20px); }
  100% {
    transform: translateY(0); } }

.App-banner {
