  flex-direction: column;
  color: white; }
  .App-banner .Header-text {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 25%;
    max-width: 70%; }
  .App-banner .background {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background-size: cover;
    background-repeat: no-repeat; }
  .App-banner .shadow {
