  .App-banner .slide-in {
    -webkit-animation: slide-in-bottom 2.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 250ms;
            animation: slide-in-bottom 2.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 250ms; }
  .App-banner .fade-in {
    -webkit-animation: fade-in 3.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 400ms;
            animation: fade-in 3.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 400ms; }
  .App-banner .kenburns-top {
    -webkit-animation: kenburns-top 15s ease-out 0.5s forwards;
            animation: kenburns-top 15s ease-out 0.5s forwards; }
  .App-banner .bouncing {
    -webkit-animation: bouncing 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 1s infinite;
            animation: bouncing 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 1s infinite; }

